.rest-switcher {
	
	&__inner {
		display: flex;
		flex-direction: column;
	}
	&__item-family {
		background: url(../img/bannerPhoto.jpg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: bottom;
	}
	&__item-company {
		background: url(../img/bannerPhoto.jpg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: bottom;
		display: none;
	}
	&__item-title {
		font-weight: 600;
		font-size: 18px;
		font-family: @ptserif;
		padding-bottom: 15px;
	}
	&__item-inner {
		text-align: center;
		margin-top: 20px;
		margin-bottom: 160px;
		padding: 5px 15px;
		background: white;
		display: inline-block;
		opacity: 0.9;
	}
	&__text {
		font-size: 16px;
		font-family: @firasans;
	}
	&__slider .slick-dots-rest-switcher li {
		// margin: 0 65px;
	}
	&__slider .slick-dots-rest-switcher li button:before {
		content: 'СЕМЕЙНЫЙ ОТДЫХ';
    	border: none;
    	width: 160px;
    	color: @grey;
    	background-color: white;
    	font-family: @firasans;
    	font-size: 15px;
    	left: -67px;
	}
	&__slider .slick-dots-rest-switcher li.slick-active button:before {
		border: none;
    	color: @orange;
    	background-color: white;
    	font-family: @firasans;
    	font-size: 15px;
	}
	&__slider .slick-dots-rest-switcher li.slick-active button:after {
		content: '';
		display: block;
		position: absolute;
		border-bottom: 2px solid @orange;
		width: 30px;
   		height: 5px;
   		top: 43px;
   		left: 0;
	}
	& .slick-btn {
		color: @grey;
		display: inline-block;
	}
}

.slick-prev {
	background-color: @orange;
}